.App{position:relative;-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-animation:emerge 1s ease-in-out;animation:emerge 1s ease-in-out;width:100vw;height:100vh;overflow-x:hidden}@-webkit-keyframes emerge{0%{opacity:0}100%{opacity:1}}@keyframes emerge{0%{opacity:0}100%{opacity:1}}.ffam-lato{font-family:"Lato",sans-serif}.ffam-mont{font-family:"Montserrat",sans-serif}.fs-xl{font-size:4rem}.fs-xlm{font-size:3.5rem}.fs-l{font-size:2rem}.fs-m{font-size:1.65rem}.fs-ms{font-size:1.17em}.fs-s{font-size:.95rem;line-height:23px;letter-spacing:.5px}.fs-xs{font-size:.85rem}.fs-xxs{font-size:.65rem}.tc-white{color:#fff}.tc-orange{color:#a96512}.tc-green{color:#84834e}.tc-gold{color:#c1b159}.tc-l-gray{color:#cdcdcd}.fw-bold{font-weight:bold}.fw-semi-bold{font-weight:600}.fw-extra-bold{font-weight:800}.fw-black{font-weight:900}.fw-regular{font-weight:400}.fw-light{font-weight:300}.test-div{height:7000px}
.top-section{position:absolute;z-index:5;top:0;left:0;display:flex;align-items:center;justify-content:center;width:100vw;height:100vh;overflow:hidden;background-color:#000}.top-section-video-wrapper{display:flex;align-items:center;justify-content:center;position:absolute;width:100vw;height:100vh;left:0}.top-section-video-wrapper video{position:absolute;width:100vw;flex-shrink:0;margin:auto}.arrow-down{height:44px;position:absolute;bottom:35px;cursor:pointer}.top-section-audio{display:flex;align-items:center;justify-content:flex-start;height:30px;width:30px;cursor:pointer;position:absolute;bottom:20px;right:20px}.top-section-audio img{height:30px}#top-section-playing{height:25px;margin-left:-3px}.top-section-kick-off{display:none;position:absolute;bottom:180px}@media screen and (max-width: 500px){.top-section-kick-off{display:block}}.top-section-old-social-wrapper{position:absolute;bottom:140px}.top-section-old-social-wrapper button{box-shadow:1px 1px 5px #000;margin:3px;background-color:rgba(0,0,0,.7)}.top-section-old-social-wrapper img{height:25px;margin:4px}.top-section-old-copyright{position:absolute;bottom:90px;text-shadow:1px 1px 5px #000}
.header{height:80px;width:100vw;position:-webkit-sticky;position:sticky;top:0;z-index:19}.header-inner{display:flex;align-items:center;justify-content:space-between;width:1500px;max-width:90vw;margin:auto}.header-links button{margin-left:1rem}.header-links button:hover{color:#c3a159}.header-logo{height:80px}
